//toggle distributors
var ALL_DISTRIBUTORS = 'all';

var lastRegionClicked = ALL_DISTRIBUTORS,
    indicator = $('#currentRegion');
// hide distributors by default and show instructions
var dlist = $('.distributorList:first');
var inst_text = 'Click map to view available distributors for that area';
var instructions = $('<p id="mapInstructions"></p>').html(inst_text);
dlist.find('address').hide();
dlist.prepend(instructions);

function changeRegion(region) {
    if (region == null) {
        $('.distributorList').children(':not(#noDistributors)').slideDown(500);
    } else if (lastRegionClicked != region) {
        var key = ALL_DISTRIBUTORS;
        //default to all distributors
        if (region.id) {
            key = region.id.replace(/.*(region_[0-9a-z])/, '$1');
        }
        //region map highlight
        indicator.fadeOut(250, function () {
            indicator.removeClass().addClass(key).fadeIn(250);
        });
        //show selected item in navigation
        $('[id*=' + lastRegionClicked + ']').removeClass('current');
        $('[id*=' + key + ']').addClass('current');
        lastRegionClicked = key;
        //update current region list
        var regionInfo = $('.distributorList').children(':not(h2)').slideUp(500).parent().find(key !== ALL_DISTRIBUTORS ? '.' + key : 'address');
        if (regionInfo.length != 0) {
            regionInfo.slideDown(500);
        } else {
            $('#noDistributors').slideDown(500);
        }
        $('#distributorRegionIdentifier').text(key.replace('_', ' '));
    }
}

$('#navigationBeta').makeSticky({
    container: '#page-content',
    offset: {
        top: 16,
        bottom: 16
    }
});

$('#navigationBeta [id*="region"], .distributorMap [id*="region"], .view-all-distributors').each(function () {
    $(this).on('click', function (e) {
        e.preventDefault();
        changeRegion(this);
    });
    $(this).one('click', function () {
        instructions.remove();
    });
});

$('#navigationBeta [id*="region"]').gotoByScroll($('.distributorMap'));

